import React, { useState, useEffect } from 'react';

import twitterIcon from '../twitter.svg';
import tumblrIcon from '../tumblr.svg';
import $ from "jquery";


var colors = [
  '#16a085',
  '#27ae60',
  '#2c3e50',
  '#f39c12',
  '#e74c3c',
  '#9b59b6',
  '#FB6964',
  '#342224',
  '#472E32',
  '#BDBB99',
  '#77B1A9',
  '#73A857',
  '#0059ff'
];

const Quotes = () => {
  const [quote, setQuote] = useState('');
  const [author, setAuthor] = useState('');

  useEffect(() => {
    getQuote()
  }, []);

  const getQuote = () => {
    let url = `https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json`;
    fetch(url)
      .then(res => res.json())
      .then(data => {
        let dataQuotes = data.quotes;
        let randomNum = Math.floor(Math.random() * dataQuotes.length);
        let randomQuote = dataQuotes[randomNum];

        setQuote(randomQuote.quote);
        setAuthor(randomQuote.author);
      })
  }

  const handleClick = () => {
    
    let randColor = colors[Math.floor(Math.random()*colors.length)];
   


    // change the color of stuff

    document.body.style.backgroundColor = randColor; 


    document.querySelector('#text').style.color = 'white';
    document.querySelector('#author').style.color = 'white';


    setTimeout(()=> {
      document.querySelector('#text').style.transition = '0.8s';
      document.querySelector('#author').style.transition = '0.8s';
      document.querySelector('#text').style.color = randColor;
      document.querySelector('#author').style.color = randColor;
    }, 300)

    setTimeout(()=> {
      document.querySelector('#text').style.transition = '0s';
      document.querySelector('#author').style.transition = '0s';
    }, 1150)



    document.querySelector('#twet-quote').style.backgroundColor = randColor;
    document.querySelector('#tumlr-quote').style.backgroundColor = randColor;
    document.querySelector('#new-quote').style.backgroundColor = randColor;


    getQuote();
  }


 
// main use effect that changes the color

  useEffect(()=> {

    $('#twet-quote').attr(
      'href',
      'https://twitter.com/intent/tweet?hashtags=quotes&related=freecodecamp&text=' +
        encodeURIComponent('"' + quote + '" ' + author)
    );


    $('#tumlr-quote').attr(
      'href',
      'https://www.tumblr.com/widgets/share/tool?posttype=quote&tags=quotes,freecodecamp&caption=' +
        encodeURIComponent(author) +
        '&content=' +
        encodeURIComponent(quote) +
        '&canonicalUrl=https%3A%2F%2Fwww.tumblr.com%2Fbuttons&shareSource=tumblr_share_button'
    );

  }, [quote])




  return (
    <>
      <div id="quote-box">
      <div id="text"><p>{quote}</p></div>
      <div id="author"><p>{author}</p></div>

      <div id="buttons">
        <div className="social-media">
          <a href="#" id="twet-quote" target='_blank'>
            <span><img src={twitterIcon} alt="" /></span>
          </a>
          <a href="#" id="tumlr-quote" target='_blank'>
            <span><img src={tumblrIcon} alt="" /></span>
          </a>
        </div>
        <button onClick={handleClick} id="new-quote">New Quote</button>
      </div>
    </div>
    
    </>
  )
}



export default Quotes;